
.Modal {
  margin-top: -10rem;
  position: fixed;
  width: 105%;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.3s ease-out;
}

.Modal-layout {
  /* display:  */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  /* align-items: center; */
  padding-top: 5rem;
  width: 100%;
  height: 75rem;
  background: rgba(255, 255, 255, 0.221);
}

.Modal-box {
  margin-top: 5rem;
  height: 50%;
  width: 40rem;
  background: white;
}

.Slideshow {
  height: 50%;
  width: 100%;
  background: yellow;
}

.Slideshow-arrows {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
  /* background: black; */
}

.adjust {
  background-color: grey;
}

.opposite {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  background: grey;
}

.Slideshow-details {
  color: black;
  font-size: 2rem;
  margin-left: 1rem;
}

.SlideShow-title {
  height: 4rem;
  /* background-color: blue; */
  /* display: flex;
  flex-direction: column;
  margin: 0;
  background: blue; */
}

.Slideshow-details-sub {
  color: #919498;
  font-size: 1rem;
  margin-top: -2.2rem;
}

.hr {
  border: 0.3px solid rgb(225, 225, 225); 
  width: 95%;
  margin-left: 0;
}

.Slideshow-description {
  font-size: 1rem;
}

.Slideshow-view {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  /* background-color: blue; */
}

.Slideshow-viewsite {
  cursor: pointer;
  width: 10rem;
  height: 2rem;
}

.leave-modal {
  color: #919498;
  cursor: pointer;
  line-height: 0;
  margin-right: 1rem;
  /* background-color: blue; */
}


