.Navigation {
  width: 100%;
  height: 2rem;
  background-color: #181818;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.logo {
  max-width: 3rem;
  margin-left: 8%;
}

.Navigation-show {
  width: 8rem;
  /* padding: 1rem; */
}

.Navigation-hover {
  color: #74F9D9;
  font-size: .8rem;
  display: none;
  text-decoration: none;
}
.Navigation-links {
  /* padding: 1rem; */
  color: #47494b;
  text-decoration: none;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.8s ease-out;
}

.Navigation-show:hover .Navigation-hover {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.Navigation-show:hover .Navigation-links {
  display: none;
}


.Navigation-logo {
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}

.Navigation-icons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 40%;
  -ms-flex-align: center;
      align-items: center;
  margin-right: 2%;

}

.active {
  color: #74F9D9;
}
/* #74F9D9 */


@-webkit-keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}

@keyframes fadeInFromNone {
  0% {
      display: none;
      opacity: 0;
  }

  1% {
      display: block;
      opacity: 0;
  }

  100% {
      display: block;
      opacity: 1;
  }
}


